import React, { useState, useEffect } from 'react';

function AdminPanel() {
  const [appointments, setAppointments] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchAppointments();
  }, []);

  const fetchAppointments = async () => {
    try {
      const response = await fetch('/api/appointments');
      const data = await response.json();
      // 
      setAppointments(data.sort((a, b) => new Date(b.created_at) - new Date(a.created_at)));
    } catch (error) {
      console.error('Error fetching appointments:', error);
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return <div className="loading">...</div>;
  }

  return (
    <div className="admin-panel">
      <div className="admin-header">
        <h2></h2>
        <p>{appointments.length}</p>
      </div>
      <div className="appointments-list">
        {appointments.length === 0 ? (
          <p className="no-data"></p>
        ) : (
          appointments.map((appointment) => (
            <div key={appointment.id} className="appointment-card">
              <div className="appointment-header">
                <h3> #{appointment.id}</h3>
                <span className="appointment-time">
                  {new Date(appointment.created_at).toLocaleString()}
                </span>
              </div>
              <div className="appointment-details">
                <p><strong></strong>{appointment.name}</p>
                <p><strong></strong>{appointment.phone}</p>
                <p><strong></strong>{appointment.email}</p>
                <p><strong></strong>{appointment.service}</p>
                <p><strong></strong>{appointment.message}</p>
              </div>
            </div>
          ))
        )}
      </div>
    </div>
  );
}

export default AdminPanel; 